import { useEffect, useState, useContext } from "react";
import { useParams, useNavigate, useLocation } from "react-router-dom";
import axiosInstance from "../../api/axiosInstance";
import Music from "../music/Music"; // Music ì»´í¬ë„ŒíŠ¸ ì„í¬íŠ¸
import MusicSearch from "../music/MusicSearch";
import useMusicSearch from "../../hooks/useMusicSearch"; // useMusicSearch í›… ì„í¬íŠ¸
import useUserInfo from "../../hooks/useUserInfo"; // useUserInfo í›… ì„í¬íŠ¸
import Button from "../common/Button"; // Button ì»´í¬ë„ŒíŠ¸ ì„í¬íŠ¸
import { parseTracks } from "../../utils/trackUtils"; // parseTracks ìœ í‹¸ í•¨ìˆ˜ ì„í¬íŠ¸
import { getImageUrl } from "../../utils/imageUtils"; 
import { TrackContext } from '../../context/TrackContext';
import "./PlaylistDetail.css"; // ìŠ¤íƒ€ì¼ íŒŒì¼ ì„í¬íŠ¸
import Header from "../common/Header";

const PlaylistDetail = () => {
    const { playlistId } = useParams();
    const navigate = useNavigate();
    const userInfo = useUserInfo(); // ì‚¬ìš©ì ì •ë³´ ê°€ì ¸ì˜¤ê¸°
    const { handlePlay, isPremium } = useMusicSearch(); // useMusicSearch í›… ì‚¬ìš©
    const location = useLocation();
    const friendInfo = location.state?.friendInfo;
    const isFriendPlaylist = !!friendInfo;

    const { addTracksToList, setIsPlaying, deviceReady } = useContext(TrackContext);

    const [state, setState] = useState({
        playlistTitle: '',
        playlistComment: '',
        playlistPhoto: null,
        playlistDate: '',
        tracksData: [],
        isEditing: false,
        user: userInfo,
    });

    useEffect(() => {
        const fetchPlaylist = async () => {
            try {
                const response = await axiosInstance.get(`/playlist/detail/${playlistId}`);
    
                // tracks ë°ì´í„°ë¥¼ íŒŒì‹±í•˜ê³  ë³€í™˜
                const trackList = parseTracks(response.data.tracks);
                
                setState(prevState => ({
                    ...prevState,
                    playlistTitle: response.data.playlist.playlistTitle,
                    playlistComment: response.data.playlist.playlistComment,
                    playlistPhoto: getImageUrl(response.data.playlist.playlistPhoto), // ì´ë¯¸ì§€ URL ì„¤ì •
                    playlistDate: response.data.playlist.playlistDate.substring(0, 10),
                    isPublic: response.data.playlist.isPublic,
                    tracksData: trackList,
                }));
                
    
            } catch (error) {
                console.error("Error fetching playlist detail", error);
            }
        };
    
        fetchPlaylist();
    }, [playlistId]);

    useEffect(() => {
        if (userInfo) {
            setState(prevState => ({
                ...prevState,
                user: userInfo,
            }));
        }
    }, [userInfo]);

    const handleEdit = () => {
        setState(prevState => ({
            ...prevState,
            isEditing: true,
        }));
    };

    const handleSave = async () => {
        const formData = new FormData();
        formData.append("playlistId", playlistId);
        formData.append("playlistTitle", state.playlistTitle);
        formData.append("playlistComment", state.playlistComment);
        formData.append("playlistDate", state.playlistDate);
        formData.append("isPublic", state.isPublic);
        formData.append("userId", state.user.userId); // ì˜ˆì‹œë¡œ userId ê°’ ì‚¬ìš©
    
        if (state.playlistPhoto && typeof state.playlistPhoto !== "string") {
            formData.append("playlistPhoto", state.playlistPhoto);
        } else {
            formData.append("existingPlaylistPhoto", state.playlistPhoto || ""); // ê¸°ì¡´ ì´ë¯¸ì§€ë¥¼ ê·¸ëŒ€ë¡œ ì‚¬ìš©
        }
    
        formData.append("tracks", state.tracksData.map(track => track.uri));
    
        try {
            await axiosInstance.post("/playlist/update", formData, {
                headers: {
                    "Content-Type": "multipart/form-data",
                },
            });
            setState(prevState => ({
                ...prevState,
                isEditing: false,
            }));
            navigate("/PlaylistPage");
        } catch (error) {
            console.error("Error updating playlist", error);
        }
    };

    const handleFileChange = (e) => {
        setState(prevState => ({
            ...prevState,
            playlistPhoto: e.target.files[0],
        }));
    };

    const handleRemoveTrack = (trackId) => {
        setState(prevState => ({
            ...prevState,
            tracksData: prevState.tracksData.filter(track => track.id !== trackId),
        }));
    };

    const handleTrackSelect = (track) => {
        setState((prev) => ({
            ...prev,
            tracksData: [...prev.tracksData, track],
        }));
    };
    
    const goBack = () => {
        navigate(-1); // ì´ì „ í˜ì´ì§€ë¡œ ì´ë™
    };
    
    const handlePlayAll = () => {
        if (state.tracksData.length === 0) {
            alert('ì¬ìƒí•  íŠ¸ë™ì´ ì—†ìŠµë‹ˆë‹¤.');
            return;
        }

        // ëª¨ë“  íŠ¸ë™ì„ ì¬ìƒ ëª©ë¡ì— ì¶”ê°€
        addTracksToList(state.tracksData);
        
        // ë””ë°”ì´ìŠ¤ê°€ ì¤€ë¹„ë˜ë©´ ì¬ìƒ ì‹œì‘
        if (deviceReady) {
            setIsPlaying(true);
        } else {
            setTimeout(() => {
                setIsPlaying(true);
            }, 1000);
        }
    };

    return (
        <div className="container1">
            <Header />
                
            <div className="playlist-detail-container">
                <div className="playlist-detail-header">
                    
                {state.isEditing ? (
                        <>
                            <input
                                className="playlist-detail-title-input"
                                value={state.playlistTitle}
                                onChange={(e) => setState(prevState => ({
                                    ...prevState,
                                    playlistTitle: e.target.value,
                                }))} 
                            />
                        </>
                    ) : (
                        <>
                            <h2 className="playlist-detail-title">{state.playlistTitle}</h2>
                        </>
                    )
                }
                </div>
                <div className="playlist-detail-body">
                    <div className="playlist-detail-left">
                        <img src={state.playlistPhoto} alt="Playlist" className="playlist-detail-photo" />
                        {state.isEditing && (
                            <input type="file" onChange={handleFileChange} accept="image/*" />
                        )}
                                
                        <div className="playlist-tracks-container">
                            {state.isEditing && (
                                <div className="music-search-container">
                                    <MusicSearch onSelectTrack={handleTrackSelect} />
                                </div>
                            )}
                            <div className={`playlist-tracks ${state.isEditing ? 'playlist-tracks-editing' : ''}`}>
                                {state.tracksData.length > 0 ? (
                                    state.tracksData.map((track, index) => {
                                        const key = `${playlistId}-${index}`;
                                        return (
                                            <div key={key} className="list-track-item">
                                                <Music track={track} handlePlay={handlePlay} isPremium={isPremium} />
                                                {state.isEditing && (
                                                    <button className="playlist-update-track-remove-button" onClick={() => handleRemoveTrack(track.id)}>âŒ</button>
                                                )}
                                            </div>
                                        );
                                    })
                                ) : (
                                    <p>ë…¸ë˜ê°€ ì—†ìŠµë‹ˆë‹¤.</p>
                                )}
                            </div>
                        </div>
                    </div>
                    <div className="playlist-detail-right">
                        {isPremium && (
                            <div className="playlist-play-controls">
                                <button 
                                    className="playlist-play-all-button"
                                    onClick={handlePlayAll}
                                    disabled={state.tracksData.length === 0}
                                >
                                    â–¶
                                </button>
                            </div>
                        )}
                        {state.isEditing ? (
                            <>
                                <textarea 
                                    className="playlist-detail-playlistComment-input"
                                    value={state.playlistComment} 
                                    onChange={(e) => setState(prevState => ({
                                        ...prevState,
                                        playlistComment: e.target.value,
                                    }))} 
                                />
                            </>
                        ) : (
                            <>
                                <p className="playlist-detail-playlistComment">
                                    {state.playlistComment}
                                </p>
                            </>
                        )}
                        <p>{state.playlistDate}</p>
                        <div className="editing-button-area">
                            {!isFriendPlaylist ? (
                                <>
                                    {state.isEditing ? (
                                        <Button text="ì €ì¥" onClick={handleSave} />
                                    ) : (
                                        <Button text="ìˆ˜ì •" onClick={handleEdit} />
                                    )}
                                </>
                            ) : null}
                            <Button text="ë’¤ë¡œê°€ê¸°" onClick={goBack} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default PlaylistDetail;